*,
:after,
:before {
  padding: 0;
  margin: 0;
  overflow: hidden
}

body {
  background: #000
}

#canvas {
  display: block;
  width: 100%;
  height: 100vh
}

.trinity {
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
}

.trinity.animate {
  transform: translate(-50%, -50%) scale(0);
  animation: scaleUp 200s linear;
  animation-fill-mode: forwards
}

.trinity__img {
  width: 100%
}

@keyframes scaleUp {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(.05)
  }
  to {
    opacity: .5;
    transform: translate(-50%, -50%) scale(1)
  }
}

.video-thumbnail {
  display: inline-block;
  cursor: pointer;
}

.video-thumbnail:before {
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  content: "\f01d";
  font-family: FontAwesome;
  font-size: 15em;
  color: #fff;
  opacity: .8;
  text-shadow: 0px 0px 30px rgba(0, 0, 0, 0.5);

}
.video-thumbnail:hover:before {
  color: #eee;
}

